import React, { Component } from 'react';
import './App.css';

class App extends Component {
  render() {
    return (
      <div>
        <Button label="check" />
        <Button label="mate" />
      </div>
    );
  }
}

class Button extends Component {
  render() {
    const { label } = this.props;

    return (
      <button className="button">
        {label}
      </button>
    );
  }
}

export default App;



